body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  background: #2bc0e4; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to left,
    #eaecc6,
    #2bc0e4
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  min-height: 10vh;
  align-items: center;
  justify-content: space-around;
  display: flex;
}
h3 {
  font-weight: 300;
}
ul {
  list-style: none;
  display: flex;
  width: 30vw;
  text-decoration: none;
  justify-content: space-around;
}
li {
  display: list-item;
  padding: 10px;
}
a {
  text-decoration: none;
  color: black;
}
li:hover {
  border: 1px solid black;
  cursor: pointer;
}

.container {
  display: flex;
  justify-content: center;
  margin-top: 3%;
}
.card {
  padding: 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  text-align: center;
  box-shadow: 5px 5px 10px #888;
  border-radius: 5px;
  background: #2bc0e4; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to left,
    #eaecc6,
    #2bc0e4
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
#choose {
  padding: 5px;
}
#temp {
  color: rgb(21, 19, 136);
  margin: 15px;
}
.temptype {
  margin-top: 5px;
}
.tempbtn {
  padding: 10px;
  width: 40px;
  border-radius: 5px;
  margin: 5px;
  background-color: white;
}
.tempbtn:focus {
  border-style: inset;
  background-color: rgb(40, 115, 150);
  color: white;
}
.tempbtn:hover {
  background-color: rgb(40, 115, 150);
}
form {
  margin-top: 2%;
  text-align: center;
}
#type {
  margin-right: 50px;
}
#city {
  font-weight: 500;
  margin: 0;
}
.addCity {
  text-align: center;
  margin-top: 3%;
}
.addbtn {
  padding: 10px 70px 10px 70px;
  border-radius: 5px;
  background-color: lightpink;
}

.box {
  display: flex;
  justify-content: center;
}
h2 {
  text-align: center;
}
.cityContainer {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.city {
  width: 75%;
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 25px 50px;
  background: #2bc0e4; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to left,
    #eaecc6,
    #2bc0e4
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  margin-top: 5%;
}
.temp {
  padding: 10px 100px 10px 100px;
  background: #2bc0e4; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to left,
    #eaecc6,
    #2bc0e4
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  margin-top: 5%;
}
#removebtn {
  background-color: lightpink;
  border-radius: 5px;
  padding: 5px;
}

